  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
     box-sizing: border-box;
}
 *, *::before, *::after {
     box-sizing: inherit;
}
 html, body {
     width: 1px; /*No idea why this works but it limits the width*/
     height: 100%;
     margin: 0;
     padding: 0;
     background-image: url(/shell-game/static/media/background.f5bf5b5e.jpg);
     -webkit-user-select: none;
   -moz-user-select: -moz-none;
    -ms-user-select: none;
        user-select: none;
}

@media screen and (max-width: 1030px) {

#memory-card-game {
grid-template-columns: auto auto auto auto !important;
left: 11vw;
}
