}
.memory-card {
  width: 15vw !important;
  height: 15vw !important;
}

}

td {
  transform: translate(10px, 10px);
}

table {
  transform: translate(163px, -130px);
}

#scoreboard {
  background-image: url(/shell-game/static/media/board.7f57a966.jpg);
  transform: translate(10px, 10px);
  width: 98vw;
  height: 36.5vh;
}

.player {
  font-size: 2.6vw;
  color: whitesmoke;
  font-family: 'Caveat Brush', cursive;
}

tr {
  line-height: 2vw;
}

#score-header {
  -webkit-filter: invert();
