/*CARD ANIMATION BASED ON https://codepen.io/Rufusmedia/pen/YpWGYB*/
 .card {
     perspective: 1000px;
     position: absolute;
     z-index: 100;
}

.current-player {
  color: yellow !important;
}

#memory-card-game {
  top: 44%;
  align-items: center;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto;
  justify-items: center;
  justify-content: center;
  position: absolute;
  height: 328px;
}

.memory-card {
  perspective: 1000px;
  margin: 0.8em;
  z-index: 100;
  width: 115px;
  height: 115px;
}
.big-card {
  height: 10.5vw;
  width: 10.5vw
}

.card-position {
  transition: transform 2s ease;
}
 .show > .innerCard {
     transform: rotateY(180deg);
}
.shuffle-buttons {
  position: fixed;
  top: 39vh;
  left:2vh;
  background: #ffffff99;
  color: #b7bdbd;
  border-radius: 4px;
  padding: 5px;
}
.switch-button {
display: inline;
margin-left: 5px;
margin-right: 5px;
}
.innerCard {
    width: 10vw;
    height: 10vw;
    transition: 1.9s;
    transform-style: preserve-3d;
    position: relative;
}
 .front, .back {
     -webkit-backface-visibility: hidden;
             backface-visibility: hidden;
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     border-radius: 5px;
     box-shadow: 5px 14px 12px -10px rgba(0,0,0,0.55);
}
.file-input, .back-button {
    transform: translate(1.19vw, 34.34vh);
    position: absolute;
    z-index: 99;
    color: whitesmoke;
    font-size: 19px;
}

#winner {
  position: fixed;
  z-index: 300;
}
#winner-msg {
  line-height: 50%;
  color: transparent;
  font-size:3em;
  z-index:400;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: rgb(245, 255, 75);
  padding-right: 8vw;
}

.trophy-icon {
    position: absolute;
    z-index: 99;
    font-size: 19px;
    transform: translate(1vw, 2vh);
    color: darkgrey;
}
.balloons {
  -webkit-filter: hue-rotate(-216deg);
          filter: hue-rotate(-216deg);
  transform: translate(220px, -100px);
}

.file-input.extra {
    transform: translate(3vw, 34.15vh);
    font-size: 15.2px;
}
#winner {
    position:fixed;
    border-radius: 20px;
    background-image: url(/shell-game/static/media/confetti.e4d2c1d0.gif);
    background-repeat: repeat;
    width: 100vw;
    height: 49vh;
    text-align: center;
}
.file-input > input {
  display: none;
}
input {
  color: grey;
}
 .back {
     background: #02b3e4;
     background-image: url(/shell-game/static/media/back.2c129c9e.png);
     background-size: contain;
     transform: rotateY(180deg);
     border-bottom: 4px solid #4c7c80;
}
 .front {
     background-color: rgb(0, 19, 96);
     z-index: 2;
}

#welcome {
  position: fixed;
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100%;
  color: #fbfbfb;
  bottom: 62%;
  flex-direction: row;
  align-items: baseline;
  z-index: 1
}

.welcome-button {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  padding-left: 15px;
}

.welcome-label {
  color: black
}

.welcome-button-pressed {
  background: none;
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  padding-left: 15px;
  color: black;
}

#image-display {
  top: 40%;
  width: 80%;
  display: flex;
  position: fixed;
  justify-content: center;
  flex-wrap:wrap;
  top: 46vh;
  left:2vh;
  background: #ffffff99;
  color: #b7bdbd;
  border-radius: 4px;
  padding: 5px;
}

.dis-image {
  width: 11vw;
  height: 11vw;
  background-color: white;
  border-radius:7px;
  margin: 3px;
}
.dis-image-more {
  width:11vw;
